.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
  opacity: 0;
  pointer-events: none;

  &__content {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    width: 50vw;
    transform: scale(0.5);
    transition: 0.4s all;

    &.active {
      transform: scale(1);
      z-index: 1;
    }
  }

  &.active {
    opacity: 1;
    pointer-events: all;
    z-index: 1;
  }
}
